.Search-container {
  
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3.34rem;
  margin-bottom: 4.33rem;
}

.Search-container input {
  font-family: 'Poppins', sans-serif;
  width: 300px;
  padding: .88rem 0.2rem;
  border: 1px solid #fff;
  border-radius: 3px;
  margin-bottom: 2.22rem;
  color: #010c3f;
  text-align: center;
  font-size: 1rem;
}

.SearchBar input:focus {
  outline: none;
}

.Search-container input::-webkit-search-cancel-button{
  cursor: pointer;
  position:relative;
  right:0px;  

  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius:10px;
  background: rgb(230, 43, 121);
}